import React from "react";
import gql from "graphql-tag";
import { Query } from "react-apollo";
import { Link, useParams } from "react-router-dom";
import classNames from "classnames";

export default function Launch() {
  let { flight_number } = useParams();
  flight_number = parseInt(flight_number);
  const LAUNCH_QUERY = gql`
  query {
    launch(flight_number: ${flight_number}) {
      flight_number
      mission_name
      launch_year
      launch_site {
        site_name
      }
      launch_success
      details
      links {
        mission_patch
        video_link
      }
    }
  }
`;
  return (
    <div>
      <h1 className="display-4 my-5">Launch</h1>
      <Query query={LAUNCH_QUERY} variables={{ flight_number }}>
        {({ loading, error, data }) => {
          if (loading) return <h4>Loading...</h4>;
          if (error) console.log(error);

          const {
            flight_number,
            mission_name,
            launch_year,
            launch_site,
            launch_success,
            details,
            links,
          } = data.launch;

          return (
            <div>
              <h1 className="display-6 my-3">
                <span className="text-dark">Mission :</span> {mission_name}
              </h1>
              <h4 className="my-5">Launch Details</h4>
              <ul className="list-group">
                <li className="list-group-item">
                  Flight Number : {flight_number}
                </li>
                <li className="list-group-item">Launch Year : {launch_year}</li>
                <li className="list-group-item">
                  Launch Site : {launch_site.site_name}
                </li>
                <li className="list-group-item">
                  Launch Success :
                  <span
                    className={classNames({
                      "text-success": launch_success,
                      "text-danger": !launch_success,
                    })}
                  >
                    {launch_success ? " Yes" : " No"}
                  </span>
                </li>
                <li className="list-group-item">
                  Details : <p>{details}</p>
                </li>
                <div className="my-5" style={styles.divMedia}>
                  <img
                    src={links.mission_patch}
                    alt="Mission patch"
                    style={styles.img}
                  />
                  <video src={links.video_link} controls></video>
                </div>
              </ul>
              <div className="my-5">
                <Link to="/" className="btn btn-secondary">
                  Back to Homepage
                </Link>
              </div>
            </div>
          );
        }}
      </Query>
    </div>
  );
}

const styles = {
  img: {
    width: "200px",
    height: "auto",
  },
  video: {
    width: "200px",
    height: "auto",
  },
  divMedia: {
    display: "flex",
    justifyContent: "space-around",
    alignItems: "center",
  },
};
